function tologo(){
	uni.showModal({
	    title: '登录提示',
	    content: '当前未登录,请前往登录',
		confirmColor:'#1296DB',
	    success: function (res) {
	        if (res.confirm) {
				uni.redirectTo({
					url:'/pages/mine/login/login'
				})
				console.log('用户点击确定');
	        } else if (res.cancel) {
	            console.log('用户点击取消');
	        }
	    }
	});
	return
}
export default tologo